*{
    padding:0;
    margin:0;
}
.main-container{
    display: flex;
    align-items: center;
    justify-content: center;
}
.container{
    margin-top:-3rem;
    text-align: center;
   box-shadow: rgba(0, 0, 0, 0.2) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;;
    height:25rem;
    width:20rem;
}
.input-field{
    margin:1rem;
    display: flex;
    flex-direction: column;
}
.input-field{
    text-align: left;
}
.input-field span{
    font-size: 1.1rem;
    margin:0.3rem;
   font-family:arial;
   color:white;
}
.container h2{
    padding:1rem;
    border-bottom: 1px solid silver;
}
input{
    
    background-color: #282c34;
    padding:0.3rem;
    padding-left: 2rem;
    
border:none;
border-radius: 0.3rem;
border-bottom:1px solid rgb(141, 141, 141);
font-size: 1.1rem;
color:cyan;
}
.icon{
    position: absolute;
    margin-top:2.2rem;
    margin-left: 0.2rem;
}
input:focus{
    outline: none;
    background-color: #282c34;
}
.submit{
    background-color: green;
    color:white;
    border:none;
    padding:0.5rem;
    font-size: 1.1rem;
    border-radius: 0.2rem;
}
.submit1{
    background-color: rgb(64, 131, 255);
    color:white;
    border:none;
    padding:0.5rem;
    font-size: 1.1rem;
    border-radius: 0.2rem;

}
#login{
font-size: 1.1rem;
color:white;
}
#login a{
    margin-left: 3px;
    color:rgb(33, 96, 255);
    font-weight: bold;
    border-bottom: 1px solid rgb(15, 45, 243);
}
#home{
   display: flex;
   justify-content: center;
  
    
}
#contact{
    text-align: center;

}

.nav-bar{
   
    height: 10vh;
    width:100%;
    background-color: #282c34;
    color:red;
    display:flex;
    justify-content:center;
    align-items: center;
    border-bottom: 1px solid rgb(57, 57, 57);
   
}
.nav-link{
    text-decoration: none;
    color:white;
    font-size: 1.1rem;
   font-family: monospace;
 
    margin:2rem;
}
.nav-link:hover{
    color:red;
}

/* .nav-bar .active{
    color:red;
    border-bottom: 2px solid red;
} */


.register{
    border-radius: 0.2rem;
    background-color: green;
    padding:0.5rem;
    animation: register 1.2s linear infinite;
}
.register:hover{
    color:white;
}
.login:hover{
    color:white;
}
.login:active{
    border-bottom:none;
    color:white;
}

.login{
    border-radius: 0.2rem;
    background-color: rgb(5, 51, 202);
    padding:0.5rem;
    animation:login 1.2s linear infinite;
}
.icon-dis{
     display:none;
}







/* mobile navbar is starting from here */
.nav-bar1{
    height: 100vh;
    width:100%;
   position: fixed;
    background-color:#282c34;
   transform: translateX(-100%);
    display:flex;
    justify-content:center;
    align-items: center;
    flex-direction: column;
   
}
.trans{
    transform: translateX(0);
    transition: all 0.5s;
}
.tran{
   
    transition: all 0.5s;
}
.nav-link1{
    text-decoration: none;
    color:white;
    font-size: 1.2rem;
   font-family: monospace;
   padding:1rem;
 border-bottom: 1px solid rgb(104, 104, 104);
    margin:2rem;
}
.register1{
    background-color: green;
    padding:0.5rem;
    color:white;
    padding-left: 2.1rem;
    padding-right: 2.1rem;
    border-radius: 0.3rem;
    animation: register 1.2s linear infinite;
}
.register1:hover{
    color:white;
}
.login1:hover{
    color:white;
}
/* animation */
@keyframes register{
    0%{
        background-color:green;
    }
    50%{
        background-color:rgb(0, 213, 0);
    }
    100%{
        background-color:green;
    }
}
@keyframes login{
    0%{
        background-color:blue;
    }
    50%{
        background-color:rgb(35, 126, 245);
    }
    100%{
        background-color:blue;
    }
}
.login1{
    border-radius: 0.3rem;
    
  
    padding:0.5rem;
    padding-left: 3rem;
    padding-right: 3rem;
    color:white;
    animation: login 1.2s linear infinite;
}

.nav2{
    border-bottom: 1px solid rgb(57, 57, 57);
    width:100%;
    height: 10vh;
    display: flex;
   background-color: #282c34;
   position: fixed;
        box-shadow: 0 2px 2px -2px rgba(0,0,0,.3);
    
    display: none;
}
#menu{
    color:white;
    font-size: 2rem;
    margin-right: 2rem;
    margin-top: 1.1rem;
}

#cross{
    color:white;
    position: absolute;
    top:1.5rem;
    right:2rem;
    font-size: 1.3rem;
    font-weight: 800;
    
}

.mcontainer{
    
    height: 100vh;
    color:white;
    background-color: #282c34;
}
.main-container{
   height: 100vh;
    color:white;
    background-color: #282c34;
}

/* mobile navbar is ending here */



#home{
    padding-top: 5rem;
}
#contact{
    padding-top: 5rem;
}



/* respoonsive is starting from here */
@media only screen and (max-width:450px){
.nav-bar{
   display: none;
}
  .nav2{
 display: block;
 text-align: right;
  
}
.nav-bar1{
    top:0;
}

.container{
   
    border:none;
    box-shadow: none;
}
.container h2{
   padding-bottom: 0.5rem;
}
#login{
    margin-top: 1rem;
}
}